<template>
  <div>
    <p>this is a homepage for Admin</p>
    <p>welcome,<label id="username"></label></p>
    <el-button type="primary" @click="logOut()" class="logOutButton">注销</el-button>
  </div>
</template>

<script>
export default {
  name: 'AdminBackground',
  mounted: function () {
    this.getUsername()
  },
  methods: {
    getUsername () {
      let username = this.getCookie('username')
      document.getElementById('username').innerText = username
    },
    logOut () {
      this.changeLoginStatus(false)
    }
  }
}
</script>

