<script>
// 全局变量和全局方法设置区
// 使用方法： this.method()
// 已知问题： 不可直接访问变量，router无法调用Vue.prototype型方法，因此对router的index.js的操作需要先写好对应函数然后引入。
// router中的使用方法：import _global from '../lib/global'
// _global.method()
var loginStatus = false

function getCookieForRouter (name) {
  var arr
  var reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
  if ((arr = document.cookie.match(reg))) {
    return (arr[2])
  } else {
    return null
  }
}
function setCookieForRouter (cName, value, expiredays) {
  var exdate = new Date()
  exdate.setDate(exdate.getDate() + expiredays)
  document.cookie = cName + '=' + escape(value) + ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString())
}
// TODO

export default {
  getCookieForRouter,
  setCookieForRouter,
  install (Vue, options) {
    Vue.prototype.getLoginStatus = function () {
      return loginStatus
    }
    Vue.prototype.changeLoginStatus = function changeLogin (status) {
      loginStatus = status
      if (loginStatus === false) {
        this.delCookie('username')
        this.delCookie('userType')
        this.$router.push('/')
      }
    }
    // 获得cookie
    Vue.prototype.getCookie = function getCOOKIE (name) {
      var arr
      var reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
      if ((arr = document.cookie.match(reg))) {
        return (arr[2])
      } else {
        return null
      }
    }

    // 设置cookie,增加到vue实例方便全局调用
    Vue.prototype.setCookie = function setCookie (cName, value, expiredays) {
      var exdate = new Date()
      exdate.setDate(exdate.getDate() + expiredays)
      document.cookie = cName + '=' + escape(value) + ((expiredays == null) ? '' : ';expires=' + exdate.toGMTString())
    }

    // 删除cookie
    Vue.prototype.delCookie = function delCookie (name) {
      var exp = new Date()
      exp.setTime(exp.getTime() - 1)
      var cval = this.getCookie(name)
      if (cval != null) {
        document.cookie = name + '=' + cval + ';expires=' + exp.toGMTString()
      }
    }
  }
}

</script>
