<template>
  <div class="loginBackgroundImg">
  <div class="loginWrapper">
  <transition name="el-fade-in-linear">
    <el-tabs v-show="loginShow" v-model="loginTabs" type="card" class="loginTab" style="background-color: rgba(255,255,255,0.8); padding-top:20px; padding-left: 50px; padding-right: 50px; padding-bottom: 20px">
      <el-tab-pane label="" name="loginTab" @keyup.enter.native="submitLoginForm('loginForm')">
        <div class="hd">
          <h2>Modern Library System</h2>
        </div>
        <div class="bd">
          <el-form :model="loginForm" :rules="loginRule" ref="loginForm">
            <el-form-item prop="userName">
              <el-input type="userName" v-model="loginForm.userName" placeholder="用户名"></el-input>
            </el-form-item>
            <el-form-item prop="pwd">
              <el-input type="password" v-model="loginForm.pwd" placeholder="密码" clearable></el-input>
            </el-form-item>
            <el-form-item prop="type">
              <el-radio v-model="userType" label="user">我是用户</el-radio>
              <el-radio v-model="userType" label="admin">我是管理员</el-radio>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitLoginForm('loginForm')" class="submitBtn">登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
    </el-tabs>
  </transition>
  </div>
  </div>
</template>

<script>
import { showMsg } from '../lib/message'

export default {
  name: 'login',
  data () {
    return {
      loginShow: false,
      loginTabs: 'loginTab',
      loginForm: {
        userName: '',
        pwd: ''
      },
      loginRule: {
        userName: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      userType: 'user'
    }
  },
  mounted: function () {
    this.loginShow = true
    if (this.getCookie('notLogin') === 'true') {
      this.setCookie('notLogin', 'false', 1000 * 60 * 60 * 24 * 15)
      showMsg(this, true, '您需要先登录才能查看该界面', 'info')
    }
  },
  methods: {
    submitLoginForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(
            {
              'username': this.loginForm.userName,
              'password': this.loginForm.pwd,
              'userType': this.userType
            }
          )
          var messageWindow = this
          this.$ajax({
            method: 'post',
            url: '/LibrarySystem/user/login',
            params: {
              'username': this.loginForm.userName,
              'password': this.loginForm.pwd,
              'userType': this.userType
            }
          }).then(function (response) {
            console.log(response)
            if (response.data === true) {
              showMsg(messageWindow, close, '登陆成功', 'info')
              let expireDays = 1000 * 60 * 60 * 24 * 15
              this.setCookie('username', this.loginForm.userName, expireDays)
              this.setCookie('userType', this.userType, expireDays)
              this.changeLoginStatus(true)
              if (this.userType === 'user') {
                this.$router.push('/Homepage')
              } else {
                this.$router.push('/AdminBackground')
              }
            } else {
              showMsg(messageWindow, close, '登陆失败，请检查用户名或密码以及账户类型是否正确', 'error')
            }
          }.bind(this)).catch(function (res) {
            showMsg(messageWindow, close, '登陆失败，请检查网络连接和用户名密码是否正确', 'info')
          })
        } else {
          showMsg(this, close, '请填写完整的信息', 'info')
        }
      })
    }
  }

}
</script>

<style scoped>
  .loginBackgroundImg{
    height: 100%;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    overflow-y: scroll;
    overflow-x: hidden;
    background: url("../assets/background.png") no-repeat;
  }
  .loginWrapper {
    position: absolute;
    top: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-family: Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,SimSun,sans-serif;
    font-size: 18px;
  }
  .loginTab {
    transition: 1s;
  }
</style>
